	module.exports = {"data":{"markdownRemark":{"html":"<h1>Example Bash Script</h1>\n<div class=\"gatsby-highlight\" data-language=\"bash\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">#!/bin/bash\n\n# Note that we don&#39;t enable the &#39;e&#39; option, which would cause the script to\n# immediately exit if &#39;run_tests&#39; failed\nset -uo pipefail\n\n# Run the main command we&#39;re most interested in\nrun_tests\n\n# Capture the exit status\nTESTS_EXIT_STATUS=$?\n\n# Run additional commands\nclean_up\n\n# Exit with the status of the original command\nexit $TESTS_EXIT_STATUS</code></pre>\n      </div>","frontmatter":{"image":"./images/code1.jpg","angle":"4"}}},"pathContext":{"absolutePath":"/Users/blubecker/ls_work/presentations/what_is_buildkite/src/pages/6.md"}}

